@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  font-family: Space Mono, sans-serif;
}

#paragrafASIO {
  margin-top: 14px;
}

#css {
  line-height: 1;
  font-family: 'Inter', serif;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
}

#content {
  line-height: 1;
  font-family: 'Inter', serif;
  color: rgba(0, 0, 0, 0.3);
  margin: 0;
  padding: 0;
  vertical-align: baseline;
  grid-area: content;
  position: relative;
  border-color: transparent;
  outline-offset: -1px;
  outline: 1px solid rgba(0, 0, 0, 0.3);
  background-color: rgba(0, 0, 0, 0.1);
}

#grid {
  line-height: 1;
  font-family: 'Inter', serif;
  color: rgba(0, 0, 0, 0.3);
  margin: 0;
  padding: 0;
  vertical-align: baseline;
  padding-bottom: 50px;
  display: grid;
  grid-template-areas: "left content right""left footer right";
  grid-template-columns: auto 700px auto;
  border-color: transparent;
  outline-offset: -1px;
  outline: 1px solid rgba(0, 0, 0, 0.3);
  background-color: rgba(255, 255, 0, 0.1);
}

#animacja {

  list-style: none;
  hyphens: auto;
  font-family: Inconsolata,monospace;
  font-style: normal;
  font-size: 16px;
  letter-spacing: .02em;
  line-height: 1.6em;
  text-align: left;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
  text-decoration: none;
  border-bottom: 2px dotted #000;
  opacity: 1;
  color: #000;
  transition-duration: 200ms;
  transition-delay: 0ms;
}